import {
    CallDirection,
    chatChannelTypes,
    ExternalKBArticleData,
    ExternalKBSearchResultData,
    InteractionActivityData,
    InteractionAssociatedObjectData,
    InteractionData,
    InteractionType,
    LoginStateData,
} from '@bpinc/adapters-public-api-types'
import {AgentDesktopClientAPIImpl} from '../../adc-client-api'

import {loadCrmApi} from '../msdyn365/loadExternalApi'
import {
    ClickToActionPayload,
    CrmActivityHistoryEntry,
    Dyn365RawKBSearchResult,
    Dyn365RawKnowledgeBaseArticle,
    PrimitiveType,
} from '../msdyn365/types'
import {utils} from '../../utils'

type InteractionDataKeys = keyof InteractionData
type ExtendedActivityData = InteractionData & { [key: string]: string }

type OneToManyReference = {
    ReferencingEntity: string
    ReferencingAttribute: string
    ReferencingEntityNavigationPropertyName: string
}

type MsdynObject = {
    objectId: string
    objectType: string
    displayName: string
}

type CifSession = {
    sessionId: string
    focused: boolean
}

const integrationKey = 'omnichannel'

const activityDataKeyMap: Partial<Record<InteractionDataKeys, string>> = {
    description: 'description',
    interactionId: 'bpattern_interaction_id',
    globalInteractionId: 'bpattern_global_interaction_id',
    // service: 'bpattern_service',
    disposition: 'bpattern_disposition',
    // recordingUrl: 'bpattern_recording_url',
    callDirection: 'directioncode',
    // DNIS: 'bpattern_dnis',
    // transferANI: 'bpattern_ani',
    phoneNumber: 'phonenumber',
    startTime: 'actualstart',
    endTime: 'actualend',
    duration: 'actualdurationminutes',
}

const activityDataTransformValueMap: Partial<Record<InteractionDataKeys, Function>> = {
    callDirection: (direction: CallDirection) => (direction === 'outbound' ? true : false),
}

const activityTypeToTableNameMap: Record<InteractionType, string> = {
    'voice': 'phonecall',
    'chat': 'bpattern_messaging_session',
    'email': 'email',
}

const maxGetObjectAttempts = 10

class Session {

    public objectList: MsdynObject[]
    public selectedObjects: MsdynObject[]
    public urlHistory: string[]

    public sessionId: string
    public interactionId: string | null

    constructor(sessionId: string, interactionId: string) {
        this.objectList = []
        this.selectedObjects = []
        this.urlHistory = []

        this.sessionId = sessionId
        this.interactionId = interactionId
    }

}

export const activityTables: Record<InteractionType, string> = {
    'voice': 'phonecall',
    'chat': 'bpattern_messaging_session',
    'email': 'email',
}

const transformDyn365KBSearchResults = (searchResults: Dyn365RawKBSearchResult[]): ExternalKBSearchResultData[] => {
    return searchResults
    .filter(item => {
        return !!item.content
    })
    .map(item => ({
        id: item.knowledgearticleid,
        type: 'article',
        title: item.title,
        text: item.content!,
    }))
}

const transformDyn365KBArticle = (articleData: Dyn365RawKnowledgeBaseArticle): ExternalKBArticleData | null => {
    if (!articleData.content) {
        return null
    }
    return {
        id: articleData.knowledgearticleid,
        title: articleData.title,
        keywords: articleData.keywords || '',
        answer: articleData.content!,
        notes: articleData.description || '',
        language: articleData.languagelocaleid.code,
        createdByUser: articleData['_createdby_value@OData.Community.Display.V1.FormattedValue'],
        lastEditedByUser: articleData['_modifiedby_value@OData.Community.Display.V1.FormattedValue'],
        customFields: [],
    }
}

const entityDisplayName: Record<string, string> = {}

export const registerOmnichannelIntegration = () => {

    const adApi = new AgentDesktopClientAPIImpl({integrationKey})

    const sessions: Session[] = []
    // sync with Agent Desktop interaction
    // not with Omnichannel session
    let currentSession: Session | null

    loadCrmApi.then(() => {

        console.log('##############Microsoft.CIFramework: ', window.Microsoft)
        console.log('###READY!STEADY!GO!')

        const cif = window.Microsoft.CIFramework

        let loginData: LoginStateData | null = null
        let currentScreenObject: {id: string; type: string} | null = null

        const extractErrorMessage = (err: unknown) => {
            if (typeof err === 'string') {
                try {
                    const errObj = JSON.parse(err)
                    if (errObj && errObj.message) {
                        return errObj.message
                    }
                    // eslint-disable-next-line no-empty
                } catch (e) {}
                return err
            } else {
                return (err instanceof Error) ? err.message : `${err}`
            }
        }

        cif.setMode(1).catch((e: string) => console.error('#ERROR-CRM-SET-MODE: ', extractErrorMessage(e)))
        cif.setWidth(500).catch((e: string) => console.error('#ERROR-CRM-SET-WIDTH: ', extractErrorMessage(e)))

        adApi.on('ON_LOGIN', async (data) => {
            loginData = data
            await cif.setClickToAct(true)
        })
        adApi.on('ON_LOGOUT', async () => {
            await cif.setClickToAct(false)
        })

        adApi.getLoginState().then(result => {
            if (result.status === 'success') {
                loginData = result.data
            } else {
                console.warn('### Cannot get login state data:', result.error)
            }
        })

        const screenPopEntity = async (entityId: string, entityName: string) => {
            console.log('#SCREEN-POP (CREATE-TAB):', entityName, ', id: ', entityId)
            try {
                const result = await cif.createTab({
                    templateName: 'entityrecord',
                    templateParameters: {
                        entityName,
                        entityId,
                    },
                    isFocused: true,
                })
                console.log('#Result: ', result)
            } catch (e) {
                console.error('#ERROR-SCREEN-POP: ', (e instanceof Error) ? e.message : `${e}`)
            }
        }

        const defaultScreenPopSearchEntityType = 'contact'
        const screenPopSearch = async (searchQuery: string, entityType = defaultScreenPopSearchEntityType) => {
            console.log('#PREY!PREY! This is the search for: ', searchQuery)
            try {
                const result = await cif.renderSearchPage(entityType, searchQuery)
                console.log('#CrmSearchResult: ', result)
            } catch (e: any) {
                console.error('#ERROR-CRM-SEARCH: ', extractErrorMessage(e))
            }
        }

        adApi.on('ON_OPEN_RECORD', record => {
            return screenPopEntity(record.id, record.type)
        })

        adApi.on('ON_SEARCH_RECORDS', data => {
            return screenPopSearch(data.text, data.types?.length ? data.types[0] : undefined)
        })

        const createBasicActivityObject = (table: string, interactionData: InteractionActivityData): CrmActivityHistoryEntry => {
            const activityRecord: Partial<Record<string, PrimitiveType>> = {
                'subject': `Bright Pattern ${interactionData.type}`,
                'description': interactionData.description,
                'actualstart': interactionData.startTime ? (new Date(interactionData.startTime)).toUTCString() : undefined,
                'actualend': interactionData.endTime ? (new Date(interactionData.endTime)).toUTCString() : undefined,
                'actualdurationminutes': interactionData.duration ? Math.ceil(interactionData.duration / 60) : 0,
                'bpattern_interaction_id': interactionData.interactionId,
                'bpattern_global_interaction_id': interactionData.globalInteractionId || '',
                'bpattern_agent': loginData ? loginData.username : undefined,
                'bpattern_service': interactionData.service,
                'bpattern_disposition': interactionData.disposition,
                'bpattern_recording_url': interactionData.playbackUrl,
                'bpattern_dnis': interactionData.DNIS,
                'bpattern_ani': interactionData.ANI,
            }
            if (table === activityTables.voice) {
                activityRecord['phonenumber'] = interactionData.phoneNumber
                activityRecord['directioncode'] = interactionData.callDirection && (
                    interactionData.callDirection === 'outbound' ? 'true' : 'false'
                )
            }
            return activityRecord
        }

        const saveActivityForEntity = async (interaction: InteractionActivityData, regardingEntity: InteractionAssociatedObjectData) => {

            const metadataStr = await cif.getEntityMetadata(regardingEntity.type)
            const metadata = JSON.parse(metadataStr)
            const tableName = activityTables[interaction.type]
            const additionalActivityData: CrmActivityHistoryEntry = {}

            console.log('#METADATA: ', metadata)

            entityDisplayName[regardingEntity.type] = metadata.DisplayName

            let phoneCallRef: {ReferencingEntityNavigationPropertyName: string} | null = null
            const entitySetName = metadata.EntitySetName,
                entityLogicalName = metadata.LogicalName,
                oneToManyRel = metadata.OneToManyRelationships

            if (oneToManyRel && oneToManyRel._collection) {

                try {
                    phoneCallRef = oneToManyRel._collection.find((item: OneToManyReference) => {
                        const entity = item.ReferencingEntity,
                            attribute = item.ReferencingAttribute
                        return entity === tableName && attribute === 'regardingobjectid'
                    })

                } catch (e) {
                    console.log('#Error in the potentially dangerous place!')
                }
            }

            if (entitySetName && phoneCallRef) {

                const name = phoneCallRef.ReferencingEntityNavigationPropertyName,
                    key = `${name}@odata.bind`,                                                     // like regardingobjectid_contact_phonecall@odata.bind
                    value = `/${entitySetName}(${regardingEntity.id})`                              // like /contacts(2ede9029-985c-e911-a980-000d3a1cabce)

                additionalActivityData[key] = value

            } else if (entityLogicalName) {

                const key = `regardingobjectid_${entityLogicalName}_${tableName}@odata.bind`,       // like regardingobjectid_contact_phonecall@odata.bind
                    value = `/${entitySetName}(${regardingEntity.id})`                              // like /contacts(2ede9029-985c-e911-a980-000d3a1cabce)

                additionalActivityData[key] = value

            } else {
                console.warn('#regardingobjectid reference is note detected')
            }

            const activity = {
                ...createBasicActivityObject(tableName, interaction),
                ...additionalActivityData,
            }
            console.log('#ACTIVITY OBJECT: ', activity)

            return cif.createRecord(tableName, JSON.stringify(activity))
        }

        const saveActivity = async (interaction: InteractionActivityData) => {

            console.log('# SAVE-ACTIVITY-HISTORY: ', interaction)

            // getEnv
            const selectedObjectId = interaction.associatedObjects.selected[0] ?? undefined
            const selectedObject =  selectedObjectId &&
                interaction.associatedObjects.list.find(obj => obj.id === selectedObjectId)

            console.log('# regarding to object: ', selectedObject)
            try {
                if (selectedObject) {
                    await saveActivityForEntity(interaction, selectedObject)
                } else {
                    const tableName = activityTables[interaction.type],
                        activity = createBasicActivityObject(tableName, interaction)

                    console.log('#Activity without regarding obj: ', activity)
                    cif.createRecord(tableName, JSON.stringify(activity))
                }
            } catch (e) {
                console.warn('Error while saving activity history record with regarding object: ', e)
                const tableName = activityTables[interaction.type]
                cif.createRecord(
                    tableName,
                    JSON.stringify(createBasicActivityObject(tableName, interaction)),
                )
            }
        }

        adApi.on('ON_INTERACTION_REMOVED', interaction => {
            if (currentSession && interaction.interactionId === currentSession.interactionId) {
                currentSession.interactionId = null
            }
        })

        adApi.on('ON_SAVE_ACTIVITY_RECORD', interaction => {
            return saveActivity(interaction)
        })

        adApi.on('ON_ACTIVE_INTERACTION_SWITCHED', interactionId => {
            console.log('# Interaction switched, id: ', interactionId)
            console.log('# sessions:', sessions)
            const correlatedSession = sessions.find(ses => ses.interactionId === interactionId)
            if (correlatedSession) {

                if (currentSession !== correlatedSession) {
                    currentSession = correlatedSession
                    console.log('# REQUEST FOCUS SESSION')
                    cif.requestFocusSession(correlatedSession.sessionId)
                }

            } else {
                currentSession = null
            }
        })

        let listenedEnv = ''
        const getObjectDataFromEnv = async () => {

            const rememberUrl = listenedEnv

            return new Promise<void>((resolve, reject) => {

                let attempt = 1
                const tryGetObjectDataFromEnv = async () => {

                    if (listenedEnv !== rememberUrl) {
                        resolve()
                        return
                    }

                    if (!currentSession?.interactionId) {
                        console.warn('No need to get entity: currentInteraction is undefined')
                        resolve()
                        return
                    }

                    const envStr = await cif.getEnvironment()
                    const env = JSON.parse(envStr)
                    console.log(`#ENTITY-ENVIRONMENT ATTEMPT #${attempt}: `, env)

                    if (env.dashboardType || (env.pagetype && env.pagetype !== 'entityrecord')) {
                        resolve()
                        return
                    }

                    if (env.id && env.etn) {

                        currentScreenObject = {id: env.id, type: env.etn}
                        adApi.setInteractionActiveScreen({
                            recordId: env.id,
                            recordType: env.etn,
                        })
                        const entityDataStr = await cif.retrieveRecord(env.etn, env.id),
                            entityData = JSON.parse(entityDataStr)
                        console.log('#obj-entity: ', entityData)

                        const displayName =
                            entityData.title ||
                            entityData.fullname ||
                            entityData.name ||
                            entityData.subject ||
                            env.id

                        if (!entityDisplayName[env.etn]) {
                            const entityMetadataStr = await cif.getEntityMetadata(env.etn)
                            const entityMetadata = JSON.parse(entityMetadataStr)
                            entityDisplayName[env.etn] = entityMetadata.DisplayName
                        }
                        const displayType = entityDisplayName[env.etn]

                        adApi.addInteractionAssociatedObject({
                            id: env.id,
                            type: env.etn,
                            displayName,
                            displayType,
                            customFields: {},
                        })
                        if (currentSession) {
                            currentSession.objectList.push({
                                objectId: env.id,
                                objectType: env.etn,
                                displayName,
                            })
                        }
                        resolve()

                    } else {

                        attempt++
                        if (attempt > maxGetObjectAttempts) {
                            reject('ERROR: Can\'t get CRM entity data')
                        } else {
                            setTimeout(tryGetObjectDataFromEnv, 1000)
                        }
                    }
                }

                tryGetObjectDataFromEnv()
            })
        }

        cif.addHandler('onpagenavigate', async (dataStr: string) => {
            if (!loginData) {
                return Promise.resolve()
            }

            if (currentSession?.interactionId) {
                const data = JSON.parse(dataStr)
                console.log('#NAVIGATION: ', data)
                currentSession.urlHistory.push(data.value)
                listenedEnv = data.value
                // getObjectDataFromEnv();
            }
            return Promise.resolve()
        })

        const sendClickToAction = (data: ClickToActionPayload, object?: InteractionAssociatedObjectData) => {
            let actionCalled = false
            for (const channel of chatChannelTypes) {
                if (data.value.startsWith(`${channel}:`)) {
                    adApi.startChat(channel, data.value.substring(channel.length + 1))
                    actionCalled = true
                }
            }
            if (actionCalled) {
                return
            }
            if (utils.startsWithAny(data.name, ['phone_', 'new_phone_', 'bpattern_phone_'])) {
                adApi.startCall(data.value, object)
                actionCalled = true
            } else {
                for (const channel of chatChannelTypes) {
                    if (utils.startsWithAny(
                        data.name,
                        [`${channel}_`, `new_${channel}_`, `bpattern_${channel}_`],
                    )) {
                        adApi.startChat(channel, data.value, object)
                        actionCalled = true
                    }
                }
            }
            if (actionCalled) {
                return
            }
            switch (data.format) {
                case 'Phone':
                    adApi.startCall(data.value, object)
                    break
                case 'SMS':
                    adApi.startChat('sms', data.value, object)
                    break
                case 'Email':
                    adApi.startEmail(data.value, object)
                    break
                default:
                    adApi.startCall(data.value, object)
                    break
            }
        }

        cif.addHandler('onclicktoact', async (dataStr: string) => {

            console.log('#CLICK')
            if (!loginData) {
                return Promise.resolve()
            }

            const data: ClickToActionPayload = JSON.parse(dataStr)
            console.log('#CLICK-TO-ACT: ', data)

            try {

                let entityId = data.entityId
                if (entityId.length && entityId[0] === '{') {
                    entityId = entityId.slice(1)
                }
                if (entityId.length && entityId[entityId.length - 1] === '}') {
                    entityId = entityId.slice(0, entityId.length - 1)
                }

                const sessionId = await cif.createSession({
                    templateName: 'bpo_call_session',
                    templateParameters: {
                        'omnichannel_customer360': {
                            entityId,
                            entityName: data.entityLogicalName,
                        },
                    },
                })

                console.log('#SESSION ID: ', sessionId)

                cif.setWidth(500)


                let entityData: any = null
                try {
                    const entityDataStr = await cif.retrieveRecord(data.entityLogicalName, entityId)
                    entityData = JSON.parse(entityDataStr)
                } catch (e) {
                    console.error('#RETRIEVE-RECORD-ERROR: ', e)
                }
                console.log('#entity: ', entityData)

                const displayName =
                    entityData
                        ? entityData.title ||
                        entityData.fullname ||
                        entityData.name ||
                        entityData.subject ||
                        entityId
                        : entityId

                cif.setSessionTitle({
                    sessionId,
                    customer: displayName,
                })

                if (!entityDisplayName[data.entityLogicalName]) {
                    const entityMetadataStr = await cif.getEntityMetadata(data.entityLogicalName)
                    const entityMetadata = JSON.parse(entityMetadataStr)
                    entityDisplayName[data.entityLogicalName] = entityMetadata.DisplayName
                }
                const displayType = entityDisplayName[data.entityLogicalName]

                const lastObjectOnScreen = {
                    id: entityId,
                    type: data.entityLogicalName,
                    displayName,
                    displayType,
                    customFields: {},
                }
                sendClickToAction(data, lastObjectOnScreen)

                cif.setAgentPresence('busy')

                // const newSession = new Session(sessionId, undefined) // interactionId
                // sessions.push(newSession)
                // currentSession = newSession

                // * listenedEnv = 'pseudo-click-to-call-url'
                // getObjectDataFromEnv();

            } catch (err) {
                console.log('#click-to-act Error: ', err)
            }

            return Promise.resolve()
        })

        adApi.on('ON_NEW_INTERACTION', () => {

            cif.getMode().then((mode: number) => {
                if (mode === 0) {
                    cif.setMode(1).catch((e: string) => console.error('#ERROR-CRM-SET-MODE: ', extractErrorMessage(e)))
                }
            }).catch((e: string) => console.error('#ERROR-CRM-GET-MODE: ', extractErrorMessage(e)))

            listenedEnv = 'pseudo-url'
            getObjectDataFromEnv().then(() => {})
        })
/*
        adApi.on('ON_MANUAL_CALL', async (phonenumber: string) => {

            console.log('### ADAPTER: MANUAL-CALL-HANDLER to: ', phonenumber)
            try {
                await cif.searchAndOpenRecords(
                    defaultScreenPopSearchEntityType,
                    // eslint-disable-next-line max-len
                    '?$select=contactid&' +
                    `$filter=mobilephone eq '${phonenumber}'` +
                    ` or telephone1 eq '${phonenumber}'` +
                    ` or telephone2 eq '${phonenumber}'` +
                    ` or telephone3 eq '${phonenumber}'`,
                    false,
                )
            } catch (e) {
                console.error('#ERROR-CRM-ODATA-SEARCH: ', extractErrorMessage(e))
            }
        })
*/

        adApi.on('ON_GET_KNOWLEDGE_BASE_FOLDER', async ({language}) => {
            try {
                const dataStr = await cif.searchAndOpenRecords(
                    'knowledgearticle',
                    '?$select=' +
                    'content' +
                    ',title' +
                    '&$expand=languagelocaleid($select=code)' +
                    '&$filter=statecode eq 3' +
                    ((language === undefined || language === 'none')
                        ? ''
                        : ` and startswith(languagelocaleid/code, '${language}')`
                    ),
                    true,
                )
                const rawData = Object.values(JSON.parse(dataStr)) as Dyn365RawKBSearchResult[]
                console.log('KB folder content:', rawData)
                return transformDyn365KBSearchResults(rawData)
            } catch (e) {
                console.error('KB Folder: cif.searchAndOpenRecords error:', extractErrorMessage(e))
            }
            return []
        })

        adApi.on('ON_SEARCH_KNOWLEDGE_BASE', async (query, language) => {
            try {
                const dataStr = await cif.searchAndOpenRecords(
                    'knowledgearticle',
                    '?$select=' +
                    'content' +
                    ',title' +
                    '&$expand=languagelocaleid($select=code)' +
                    '&$filter=statecode eq 3 ' +
                    ((language === undefined || language === 'none')
                        ? ''
                        : `and startswith(languagelocaleid/code, '${language}') `
                    ) +
                    'and (' +
                    `contains(content, '${query}') or ` +
                    `contains(keywords, '${query}') or ` +
                    `contains(description, '${query}') or ` +
                    `contains(title, '${query}')` +
                    ')',
                    true,
                )
                const rawData = Object.values(JSON.parse(dataStr)) as Dyn365RawKBSearchResult[]
                console.log('#CRM-SEARCH-RESULT: ', rawData)
                return transformDyn365KBSearchResults(rawData)
            } catch (e: any) {
                console.error('#ERROR-CRM-ODATA-KB-SEARCH: ', extractErrorMessage(e))
            }
            return []
        })

        adApi.on('ON_GET_KNOWLEDGE_BASE_ARTICLE', async (articleId: string) => {
            try {
                const dataStr = await cif.searchAndOpenRecords(
                    'knowledgearticle',
                    '?$select=' +
                    'articlepublicnumber' +
                    ',createdon' +
                    ',content' +
                    ',description' +
                    ',isinternal' +
                    ',keywords' +
                    ',modifiedon' +
                    ',statecode' +
                    ',statuscode' +
                    ',title' +
                    ',versionnumber' +
                    ',createdby' +
                    ',modifiedby' +
                    ',_createdby_value' +
                    ',_modifiedby_value' +
                    ',_ownerid_value' +
                    ',_parentarticlecontentid_value' +
                    ',_rootarticleid_value' +
                    ',_subjectid_value' +
                    '&$expand=languagelocaleid($select=code)' +
                    ',transactioncurrencyid($select=isocurrencycode)' +
                    ',owningbusinessunit($select=name)' +
                    `&$filter=knowledgearticleid eq '${articleId}'`,
                    true,
                )
                const rawArticleArr = Object.values(JSON.parse(dataStr)) as Dyn365RawKnowledgeBaseArticle[]
                if (!rawArticleArr.length) {
                    return null
                }
                console.log('#CRM-KB-ARTICLE: ', rawArticleArr[0])
                return transformDyn365KBArticle(rawArticleArr[0])
            } catch (e: any) {
                console.error('#ERROR-CRM-ODATA-KB-ARTICLE: ', extractErrorMessage(e))
            }
            return null
        })

        cif.addHandler('onSessionSwitched', async (dataStr: string) => {

            const data: CifSession = JSON.parse(dataStr)
            console.log('### ON-SESSION-SWITCHED: ', data)
            console.log('### current session: ', currentSession ? currentSession.sessionId : 'NO')
            if (data.focused && currentSession && currentSession.sessionId !== data.sessionId) {

                const session = sessions.find(ses => ses.sessionId === data.sessionId)
                if (session?.interactionId) {
                    console.log('#call switchActiveInteraction (middle) for session: ', session)
                    const result = await adApi.switchActiveInteraction(session.interactionId)
                    if (result.status === 'success') {
                        currentSession = session
                    }
                } else {
                    console.error('### SMTHG GOES WRONG')
                }
            }
        })

        cif.addHandler('onSessionClosed', async (dataStr: string) => {

            const data: CifSession = JSON.parse(dataStr)
            console.log('### ON-SESSION-CLOSED: ', data)
            // It doesn't work for some reason

            const session = sessions.find(ses => ses.sessionId === data.sessionId)
            if (session?.interactionId) {
                const result = await adApi.completeInteraction(session.interactionId)
                if (result.status === 'success') {
                    const delIndex = sessions.indexOf(session)
                    sessions.splice(delIndex, 1)
                    if (currentSession === session) {
                        if (sessions.length) {
                            currentSession = sessions[sessions.length - 1]
                        } else {
                            currentSession = null
                        }
                    }
                }
            }

        })

        cif.addHandler('onmodechanged', (dataStr: string) => {
            if (!adApi.adcFrame) {
                return
            }
            const {value} = JSON.parse(dataStr)
            switch (value) {
                case 0:
                    adApi.setWidgetMinimized(true)
                    break
                case 1:
                    adApi.setWidgetMinimized(false)
                    break
                default:
                    break
            }
            return Promise.resolve()
        })


        adApi.on('ON_WIDGET_MINIMIZED_CHANGE', (widgetMinimized: boolean) => {
            cif.setMode(widgetMinimized ? 0 : 1).catch((e: string) => {
                console.error('#ERROR-CRM-SET-MODE: ', extractErrorMessage(e))
            })
        })

        adApi.on('ON_SHOW_SCREEN', screen => {
            if (screen && screen.recordId && screen.recordType) {
                screenPopEntity(screen.recordId as string, screen.recordType as string)
            }
        })

    }).catch((e: Error) => {
        console.error('Can\'t load Channel Integration Framework client API library: ', e)
    })
}
